@charset "UTF-8";
html {
  font-size: 16px; }

body,
td,
th,
input,
textarea {
  color: #000000;
  font-family: 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', Osaka, Tahoma, Verdana, Arial, Verdana, sans-serif;
  line-height: 1.5; }

body {
  overflow-wrap: break-word; }

a,
a:link,
a:visited,
a:active,
a:hover,
a:hover img {
  color: #000000;
  text-decoration: underline;
  /*
  filter: alpha(opacity=80);
  -ms-filter: "alpha(opacity=80)";
  opacity: .80;
*/ }

a:hover {
  text-decoration: none; }

a:hover,
a:hover img,
.img_hover:hover {
  /*
  filter: alpha(opacity=80);
  -ms-filter: "alpha(opacity=80)";
  opacity: .80;
*/ }

img {
  vertical-align: bottom; }

.img_ck {
  font-size: rem(1);
  line-height: 0; }

/* clearfix (micro) */
.clearfix:before,
.clearfix:after {
  content: ' ';
  display: table; }

.clearfix:after {
  clear: both; }

/* For IE 6/7 only */
.clearfix {
  *zoom: 1; }

div,
p {
  /*word-break: break-all;*/ }

h2,
h3,
h4,
h5,
h6,
div,
p,
ul,
ol,
li,
dl,
dt,
dd,
img,
nav {
  margin: 0;
  padding: 0; }

ul,
ol,
li {
  list-style: none; }
